<h1 id="生物认证" tabindex="-1"><a class="header-anchor" href="#生物认证" aria-hidden="true">#</a> 生物认证</h1>
<h2 id="生物认证秘钥签名验证" tabindex="-1"><a class="header-anchor" href="#生物认证秘钥签名验证" aria-hidden="true">#</a> 生物认证秘钥签名验证</h2>
<blockquote>
<p>https://developers.weixin.qq.com/miniprogram/dev/api-backend/open-api/soter/soter.verifySignature.html</p>
</blockquote>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">soter</span><span class="token operator">-></span><span class="token function">verifySignature</span><span class="token punctuation">(</span><span class="token variable">$openid</span><span class="token punctuation">,</span> <span class="token variable">$json</span><span class="token punctuation">,</span> <span class="token variable">$signature</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>返回值示例:</p>
<div class="language-json ext-json line-numbers-mode"><pre v-pre class="language-json"><code><span class="token punctuation">{</span>
    <span class="token property">"is_ok"</span><span class="token operator">:</span> <span class="token boolean">true</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><p>参数说明:</p>
<blockquote>
<ul>
<li>string $openid - 用户 openid</li>
<li>string $json - 通过 <a href="https://developers.weixin.qq.com/miniprogram/dev/api/open-api/soter/wx.startSoterAuthentication.html" target="_blank" rel="noopener noreferrer">wx.startSoterAuthentication<OutboundLink/></a> 成功回调获得的 resultJSON 字段</li>
<li>string $signature - 通过 <a href="https://developers.weixin.qq.com/miniprogram/dev/api/open-api/soter/wx.startSoterAuthentication.html" target="_blank" rel="noopener noreferrer">wx.startSoterAuthentication<OutboundLink/></a> 成功回调获得的 resultJSONSignature 字段</li>
</ul>
</blockquote>
